<template>
    <el-container style="height: 100%;">
        <el-aside width="100px" class="menu-box" style="background-color: #001135; line-height: 60px; color:#fff;">
            <div class="logo"></div>
            <el-menu @select="menuSelect" :default-active="menusData[activeMenuIndex].sign">
                <el-menu-item v-for="item in menusData" :key="item.sign" :index="item.sign">
                    <i :class="item.icon"></i>
                    <span>{{item.groupName}}</span>
                </el-menu-item>
            </el-menu>
        </el-aside>

        <el-container>
            <el-aside width="140px" style="background-color: #061D48; color: #fff;" class="sub-menu-box">
                <h1>{{$route.meta.title || '信息面板'}}</h1>
                <el-menu @select="subMenuSelect" :default-active="willToPath">
                    <el-menu-item 
                        v-for="(item) in menusData[activeMenuIndex].subMenus"
                        :key="item.path"
                        :index="item.path"
                    >
                        
                        <span>{{item.title}}</span>
                    </el-menu-item>
                </el-menu>
            </el-aside>
            <el-container>
                <el-header class="header-bar" style="text-align: right; line-height:60px; font-size: 16px">
                    <el-row>
                        <!-- <el-col :span="12">
                            <h1>{{$route.meta.title || '信息面板'}}</h1>
                        </el-col> -->
                        <el-col :span="24">
                            <span>{{userName}} | {{identity}}</span>
                            <span @click="quite" :style="{cursor: 'pointer'}">退出</span>
                        </el-col>
                    </el-row>
                </el-header>
                <tags-view/>
                <el-main>
                    <router-view></router-view>
                </el-main>
            </el-container>
        </el-container>
    </el-container>
</template>

<script>
import {getMenus} from '../router/routes'
import TagsView from '@/components/TagsView'

export default {
    data() {
        window.aa = this;
        return {
            menusData: getMenus(),
            activeMenuIndex: 0,
            activeSubMenuIndex: 0
        };
    },
    props: ['userName', 'identity'],
    components: {
        TagsView
    },
    computed: {
        willToPath(){
            return this.menusData[this.activeMenuIndex].subMenus[this.activeSubMenuIndex].path
        }
    },
    methods: {
        menuSelect(index){
            this.activeMenuIndex = this.menusData.findIndex(item => item.sign === index);
            this.activeSubMenuIndex = 0;    
            this.$router.push(this.willToPath);
        },
        subMenuSelect(index){
            this.activeSubMenuIndex = this.menusData[this.activeMenuIndex].subMenus.findIndex(item => item.path === index);
            this.$router.push(this.willToPath);
        },
        getPosFromUrl(){
            let path = this.$route.path;
            let menuIndex = 0;
            let subMenuIndex = 0;
            for(let i = 0; i < this.menusData.length; i++){
                let index = this.menusData[i].subMenus.findIndex(item => path.indexOf(item.path) !== -1);
                if(index !== -1){
                    menuIndex = i;
                    subMenuIndex = index;
                }
            }
            this.activeMenuIndex = menuIndex;
            this.activeSubMenuIndex = subMenuIndex;
        },
        quite(){
            sessionStorage.removeItem('loginInfo');
            this.$router.push('/login');
        }
    },
    mounted(){
        this.getPosFromUrl();
    },
    watch:{
        '$route'(){
            this.getPosFromUrl();
        }
    }
};
</script>

<style scoped lang="less">
.menu-box {
    .logo {
        height: 107px;
    }
    .el-menu-item {
        width: 100px;
        height: 100px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        line-height: 2;
        position: relative;
        i {
            display: block;
            width: 24px;
            font-size: 24px;
        }
        &.is-active {
            background: rgb(6, 29, 72);
            &::before {
                content: '';
                width: 2px;
                height: 100%;
                background: #fff;
                position: absolute;
                left: 0;
                top: 0;
            }
        }
    }
}
.sub-menu-box {
    h1 {
        line-height: 107px;
        text-align: center;
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    }
    .el-menu-item {
        position: relative;
        height: 45px;
        line-height: 45px;
        font-size: 14px;
        &.is-active {
            // background: #1890ff;
            &::before {
                content: '';
                width: 4px;
                height: 50%;
                background: #1890FF;
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
            }
        }
    }
}
.header-bar {
    background: #fff;
    border-bottom: 1px solid #eee;
}
.el-footer{
    border-top: 1px solid #eee;
}
.el-menu {
    background: none;
    border-right: none;
}
.el-menu-item {
    color: #7d8399;
}
.el-menu-item.is-active {
    border-left: none;
    color: #fff;
}
.el-menu-item:focus, .el-menu-item:hover{
    background: #061D48;
}
.el-main {
    background: #F0F2F5;
}
</style>
